import { defineStore } from 'pinia'

export default defineStore('statusStore', {
  state: () => ({
    isLoading: [],
    cartLoadingItem: '',
    messages: []
  }),
  actions: {
    // ToastMessages.vue
    // 
    PushManager (res, title = '') {
      if (res == '' || res.data.success) {
        const obj = {
          style: 'success',
          title: `${title}`
        }
        this.messages.push(obj)
      } else {
        // 
        const message = typeof res.data.message === 'string'
          ? [res.data.message]
          : res.data.message
        const obj = {
          style: 'danger',
          title: `${title}`,
          content: message.join('')
        }
        this.messages.push(obj)
      }
    }
  }
})
